.form {
  height: 100%;
  width: 100%;
  padding-top: 3rem;
  padding-left: 3rem;
}
.InputContainer {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 0.2rem;
  font-size: 0.92rem;
}
.Inputli {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
  height: 4rem;
  color: hsl(0, 0%, 0%, 0.8);
}
.Inputli input {
  width: 80%;
  flex-grow: 1;
  border: 1px solid hsl(0, 0%, 0%, 0.2);
  padding-left: 1rem;
}

.Inputli input:focus {
  outline: none;
}
.downloadButton {
  margin-top: 2rem;
  width: 12rem;
  padding: 1rem 2rem;
}
